<template>
    <div class="d-flex justify-center">  
        <form enctype="multipart/form-data" @submit.prevent="verificar">
        
           
            
        <v-card  
        class="elevation-0 mb-10 pa-4" max-width="350" >
        <v-img v-if="$vuetify.theme.dark" class="ma-auto" src="@/assets/logo-dark.jpg" width="200" />
        <v-img v-else class="ma-auto" src="@/assets/logo-light.jpg" width="200" />
            <div class="text-h4 text-center">
                Entrar
            </div>
            <v-card-text>
                <v-row no-gutters>

                    <v-col cols="12" sm="12">
                        <v-text-field 
                        type="text"
                        required
                        :rules="nomeRegra"
                        v-model="$store.state.auth.usuario.nome"
                        label="usuário"
                        prepend-icon="mdi-account"
                        hint="Informe o usuário">
                        </v-text-field>
                    </v-col>

                    <v-col cols="12" sm="12">
                        <v-text-field 
                        required
                        type="password"
                        :rules="passwordRegra"
                        v-model="$store.state.auth.usuario.senha"
                        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show1 ? 'text' : 'password'"
                        @click:append="show1 = !show1"
                        label="Palavra-passe"
                        prepend-icon="mdi-lock"
                        hint="Informe a Palavra-passe">
                        </v-text-field>                        
                    </v-col>

                </v-row>
                    

            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" tile type="submit" > entrar </v-btn>
                <v-spacer></v-spacer>
                
            </v-card-actions>  
            
            <div class="text-center mt-3">



                <router-link class="text-decoration-none" to=""> Esqueceu a palavra-passe </router-link>
                
            </div>
        </v-card> 
        <div class="text-center">
            Ainda não tens conta? 
            <router-link class="text-decoration-none" :to="{ name:'registrar' }">  crie uma </router-link>
        </div>            
        </form>
    </div>
  </template>

  <style scoped>
  
  </style>
  
  <script>
    import { mapActions, mapGetters } from 'vuex'
    import axios from '@/libs/axios'
    import { deleteCookie } from '@/services/cookie'
  export default {
    data(){
        return{
            loading:false,
            show1:false,
                valid:true,
                 nomeRegra: [
                    v => !!v || 'Informe o nome de usuário!',
                ],
                 passwordRegra: [
                    v => !!v || 'Informe a palavra-passe do usuário!',
                ],
        } 
        
    },

    methods:{
        ...mapActions({
            autenticar:'auth/autenticarClient',
        }),
        async verificar(){
            try {

                await this.autenticar()

                this.$router.push({ name: 'home' })

                
            } catch (error) {
             alert(error)   
            }
        }

     },
    mounted() {

        

    },
    
  }
  </script>