<template>
    <div class="d-flex justify-center">  
        <form enctype="text/plain" @submit.prevent="registrar">
        <v-card  
        class="elevation-0 mb-10 pa-4" max-width="350" >

        <v-img v-if="$vuetify.theme.dark" class="ma-auto" src="@/assets/logo-dark.jpg" width="200" />
        <v-img v-else class="ma-auto" src="@/assets/logo-light.jpg" width="200" />

            <div class="text-h4 text-center">

                Criar Conta
                
            </div>
            <v-card-text>
                <v-row no-gutters>

                    <v-col cols="12" sm="12">
                        <v-text-field 
                        type="text"
                        label="Nome completo"
                        required
                        v-model="$store.state.auth.inscrever.nome"
                        prepend-icon="mdi-account">
                        </v-text-field>
                    </v-col>

                    <v-col cols="12" sm="12">
                        <v-text-field 
                        type="tel"
                        required
                        v-model="$store.state.auth.inscrever.telefone"
                        label="Telefone"
                        prepend-icon="mdi-phone">
                        </v-text-field>
                    </v-col>

                    <v-col cols="12" sm="12">
                        <v-text-field 
                        type="email"
                        v-model="$store.state.auth.inscrever.email"
                        label="E-mail"
                        prepend-icon="mdi-email">
                        </v-text-field>
                    </v-col>

                    <v-col cols="12" sm="12">
                        <v-text-field 
                        type="password"
                        :rules="passwordRegra"
                        v-model="$store.state.auth.inscrever.senha"
                        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show1 ? 'text' : 'password'"
                        @click:append="show1 = !show1"
                        label="Palavra-passe"
                        prepend-icon="mdi-lock">
                    </v-text-field>                        
                </v-col>
                
                <v-col cols="12" sm="12">
                    <v-text-field 
                        label="Reeconfirmar palavra-passe"
                        type="password"
                        :rules="passwordRegra2"
                        :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show2 ? 'text' : 'password'"
                        @click:append="show2 = !show2"
                        prepend-icon="mdi-lock">
                        </v-text-field>                        
                    </v-col>

                </v-row>
                    

            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" tile type="submit" > Inscrever-se </v-btn>
                <v-spacer></v-spacer>
                
            </v-card-actions>  
            
        </v-card> 
        <div class="text-center mb-4">
            já tens uma conta? 
            <router-link class="text-decoration-none" :to="{ name:'entrada' }">  entrar </router-link>
        </div>            
    </form>
    </div>
  </template>


  
  <script>
      
    import { mapActions, mapGetters } from 'vuex'
    import axios from '@/libs/axios'
    import {  createCookie , getCookie } from '@/services/cookie/index.js'
  
    export default {
    data(){
        return{
            loading:false,
            show1:false,
            show2:false,
                valid:true,
                 nomeRegra: [
                    v => !!v || 'Informe o nome de usuário!',
                ],
                 passwordRegra: [
                    v => !!v || 'Informe a palavra-passe do usuário!',
                ],
                 passwordRegra2: [
                    v => !!v || 'Informe a palavra-passe do usuário!',
                    v => this.senhaigual === v || 'Informe a palavra-passe não consta acima',
                ],
        } 
        
    },
    computed: {
        senhaigual:function(){
            return this.$store.state.auth.inscrever.senha
        }
    },
    methods:{
        async registrar(){
            try {
                
                await this.inscrever()

                alert(`Inscrito com sucesso`)

            } catch (error) {
                
                alert(error)
                
            }

        },
        ...mapActions({
            inscrever:'auth/inscrever'
        })

     },
    mounted() {
  
    },
    
  }
  </script>