import linguas from '@/data/linguagem.json'
import axios from '@/libs/axios'

export default {
    namespaced: true,
    state:{
        usuario:{},

     },
    getters:{
        usuario:({ usuario })=>{
            return usuario
        }

    },
    mutations: {
        COMMIT_USUARIO(state, payload){
            
            state.usuario = payload
        }

    },
    actions:{
        getUsuarioData( { getters, state,commit } ){
            return new Promise(async (resolve,reject) => {
                try {

                    const query = (await axios.get('/usuario')).data


                    //console.log(query)
                    commit('COMMIT_USUARIO', query)
                    resolve('ok')
                } catch (error) {
                    
                    reject(error)
                }
            })
        }

     }
}