import Vue from 'vue'
import {  createCookie , getCookie, deleteCookie,tokenClientLabel } from '@/services/cookie/index.js'
import axios from 'axios'
const baseURL = `${/* 'http://192.168.2.2:3030' */ location.origin}/api`


const axiosIns = axios.create({
  // You can add your headers here
  // ================================
  // timeout: 3000,
  baseURL,
  headers: {'User-token': getCookie(tokenClientLabel) || false }
})

Vue.prototype.$http = axiosIns

export default axiosIns
