import axios from '@/libs/axios'
import { getCookie,createCookie,tokenClientLabel } from '@/services/cookie'


export default {
    namespaced: true,
    state:{
        usuario:{
            nome:'',
            senha:''
        },
        inscrever:{
            nome:'',
            senha:'',
            email:'',
            telefone:'',
        }

    },
    getters:{

    },
    mutations: {

    },
    actions:{
        inscrever( { state,commit,dispatch } ){
            return new Promise(async (resolve,reject) => {
                try {
                
                    const  usuario = state.inscrever

                    const response = (await axios.post('/usuario/inscrever',{},{ params: usuario })).status
                    
                    console.log( response )



                    resolve(response)

                } catch (error) {

                    reject(error)
                
                }
            })
        },

        autenticarClient({state,commit,dispatch } ){
            return new Promise(async (resolve,reject) => {
                try {
                
                    const { usuario } = state

                    const { token } = (await axios.get('/usuario/auth',{ params: usuario })).data
                    
                    console.log( token )

                    await dispatch('addClientCookie', token)


                    resolve(token)

                } catch (error) {

                    reject(error)
                
                }
            })
        },

        addClientCookie({state,commit,dispatch }, token ){
            return new Promise(async (resolve,reject) => {
                try {

                    createCookie(tokenClientLabel , token, 1)

                    resolve(token)

                } catch (error) {

                    reject(error)
                
                }
            })
        }

    }
}