import Vue from 'vue'
import VueRouter from 'vue-router'
import MainApp from '@/views/pages/MainApp.vue'
import HomeView from '../views/pages/HomeView.vue'
import Dicionario from '../views/pages/dicionario.vue'
import Perfil from '../views/pages/Perfil'
import Aula from '../views/pages/Aula.vue'
import Biblioteca from '../views/pages/Biblioteca.vue'
import AuthPage from '@/views/pages/Auth/index.vue'
import Entrada from '@/views/pages/Auth/Entrada.vue'
import Registro from '@/views/pages/Auth/Registrar.vue'
import { deleteCookie, getCookie, createCookie,isUserLoggedIn } from '@/services/cookie'

Vue.use(VueRouter)

const routes = [
  {
    path: '/autenticacao',
    redirect:{ name: "entrada" },
    component:AuthPage,
    children:[
      {
        path: 'entrada',
        name: 'entrada',
        component: Entrada
      },
      {
        path: 'registrar',
        name: 'registrar',
        component: Registro
      },
    ]
  },
  
  {
    path: '/',
    redirect:{ name: "home" },
    component:MainApp,
    meta: { requiresAuth: false },
    children:[
      {
        path: '/home',
        name: 'home',
        component: HomeView
      },
      {
        path: '/perfil',
        name: 'perfil',
        meta: { requiresAuth: true },
        component: Perfil,
      },
      {
        path: '/dicionario',
        name: 'dicionario',
        meta: { requiresAuth: false },
        component: Dicionario
      },
      {
        path: '/biblioteca',
        name: 'biblioteca',
        redirect:{ name:'livros' },
        meta: { requiresAuth: false },
        component: Biblioteca,
        children:[
            {
              path:'livros',
              name:'livros',
              meta: { requiresAuth: false },
              component: ()=>import('../views/pages/Biblioteca/Livro.vue')
              
            },
            {
              path:'videos',
              name:'videos',
              meta: { requiresAuth: false },
              component: ()=>import('../views/pages/Biblioteca/Video.vue')
              
            },
            {
              path:'audios',
              name:'audios',
              meta: { requiresAuth: false },
              component: ()=>import('../views/pages/Biblioteca/Audio.vue')
              
            },
            {
              path:'descarregados',
              name:'descarregados',
              meta: { requiresAuth: false },
              component: ()=>import('../views/pages/Biblioteca/Descarregado.vue')
              
            }
        ]
      },
      {
        path: '/aulas',
        name: 'aulas',
        meta: { requiresAuth: true },
        component: Aula
      },

      {
        path: '/nao-autorizado',
        name: 'nao-autorizado',
        meta: { requiresAuth: false },
        component: ()=>import('../views/pages/infoPage/NeedAthentication.vue')
      },


    ]
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})


router.beforeEach((to, from, next) => {

  const needAuth = (to.matched.some(record => record.meta.requiresAuth))


  if(needAuth){
    
        if (isUserLoggedIn()) {
          
          next()
    
        } else {
    
          next({ name:  'nao-autorizado' })
        }
        
    }
  next()
   


})


export default router
